/**
 * External Dependencies
 */
import classnames from 'classnames'; //npm install classnames --save

/**
 * WordPress Dependencies
 */
const { createHigherOrderComponent } = wp.compose;
const { Fragment } = wp.element;
const { InspectorControls } = wp.blockEditor;
const { PanelBody, PanelRow, SelectControl, Button, ButtonGroup } = wp.components;
const { addFilter } = wp.hooks;
const { __ } = wp.i18n;

//restrict to specific block names
const allowedBlocks = [ 'core/columns', 'core/column' ];

const marginTopControlOptions = [
    {
        caption: __( 'Default' ),
        value: '',
    },
    {
        caption: __( 'S' ),
        value: 'margin-top-small',
    },
    {
        caption: __( 'M' ),
        value: 'margin-top-medium',
    },
    {
        caption: __( 'L' ),
        value: 'margin-top-large',
    },
    {
        caption: __( 'None' ),
        value: 'margin-top-none',
    },
];

const marginBottomControlOptions = [
    {
        caption: __( 'Default' ),
        value: '',
    },
    {
        caption: __( 'S' ),
        value: 'margin-bottom-small',
    },
    {
        caption: __( 'M' ),
        value: 'margin-bottom-medium',
    },
    {
        caption: __( 'L' ),
        value: 'margin-bottom-large',
    },
    {
        caption: __( 'None' ),
        value: 'margin-bottom-none',
    },
];

const paddingTopControlOptions = [
	{
		caption: __( 'Default' ),
		value: '',
	},
	{
		caption: __( 'S' ),
		value: 'padding-top-small',
	},
	{
		caption: __( 'M' ),
		value: 'padding-top-medium',
	},
	{
		caption: __( 'L' ),
		value: 'padding-top-large',
	},
	{
		caption: __( 'None' ),
		value: 'padding-top-none',
	},
];

const paddingBottomControlOptions = [
	{
		caption: __( 'Default' ),
		value: '',
	},
	{
		caption: __( 'S' ),
		value: 'padding-bottom-small',
	},
	{
		caption: __( 'M' ),
		value: 'padding-bottom-medium',
	},
	{
		caption: __( 'L' ),
		value: 'padding-bottom-large',
	},
	{
		caption: __( 'None' ),
		value: 'padding-bottom-none',
	},
];

/**
 * Add custom attributes for spacings.
 *
 * @param {Object} settings Settings for the block.
 *
 * @return {Object} settings Modified settings.
 */
function addAttributes( settings ) {

	//check if object exists for old Gutenberg version compatibility
	//add allowedBlocks restriction
	if( typeof settings.attributes !== 'undefined' && allowedBlocks.includes( settings.name ) ){

		settings.attributes = Object.assign( settings.attributes, {
			marginTop:{
				type: 'text',
				default: false,
			},
			marginBottom:{
				type: 'text',
				default: false,
			},
			paddingTop:{
				type: 'text',
				default: false,
			},
			paddingBottom:{
				type: 'text',
				default: false,
			}
		});

	}

	return settings;
}

/**
 * Add spacing controls on Advanced Block Panel.
 *
 * @param {function} BlockEdit Block edit component.
 *
 * @return {function} BlockEdit Modified block edit component.
 */


const withAdvancedControls = createHigherOrderComponent( ( BlockEdit ) => {
	return ( props ) => {

		const {
			name,
			attributes,
			setAttributes,
			toggled
		} = props;

		const {
			marginTop,
			marginBottom,
			paddingTop,
			paddingBottom,
		} = attributes;

		return (
            <Fragment>
                <BlockEdit { ...props } />
				{ allowedBlocks.includes( name ) &&
                <InspectorControls>
					<PanelBody
						title={ __( 'Vertical Margins' ) }
						initialOpen={ false }
					>
					<PanelRow>
				      <strong>Top</strong> <ButtonGroup>
				            { marginTopControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.marginTop == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {marginTop: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					  <PanelRow>
				      <strong>Btm</strong> <ButtonGroup>
				            { marginBottomControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.marginBottom == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {marginBottom: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					</PanelBody>
					<PanelBody
						title={ __( 'Vertical Paddings' ) }
						initialOpen={ false }
					>
					<PanelRow>
				      <strong>Top</strong> <ButtonGroup>
				            { paddingTopControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.paddingTop == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {paddingTop: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					  <PanelRow>
				      <strong>Btm</strong> <ButtonGroup>
				            { paddingBottomControlOptions.map( item => (

				                 <Button
				                      key={item.value}
				                      isPrimary={attributes.paddingBottom == item.value}
									  value={ item.value }
								      onClick={ () => {
											setAttributes( {paddingBottom: item.value} );
											toggled === item.value ? null: item.value;
								        } }
								 >
		                        {item.caption}
				                  </Button>
								  ))}
				      </ButtonGroup>
					  </PanelRow>
					</PanelBody>
                </InspectorControls>
				}
            </Fragment>
		);
	};
}, 'withAdvancedControls');

/* Apply the spacings in the editor too */  // https://wordpress.stackexchange.com/questions/324091/
const withSpacingClasses = createHigherOrderComponent( ( BlockListBlock ) => {
    return ( props ) => {
        if ((props.attributes.marginTop) || (props.attributes.marginBottom) || (props.attributes.paddingTop) || (props.attributes.paddingBottom))  {

			var spacingClasses = "";

	        if (props.attributes.marginTop){
				spacingClasses = spacingClasses+props.attributes.marginTop+' ';
			}
	        if (props.attributes.marginBottom){
				spacingClasses = spacingClasses+props.attributes.marginBottom+' ';
			}
	        if (props.attributes.paddingTop){
				spacingClasses = spacingClasses+props.attributes.paddingTop+' ';
			}
	        if (props.attributes.paddingBottom){
				spacingClasses = spacingClasses+props.attributes.paddingBottom+' ';
			}

			if (spacingClasses){
				spacingClasses = spacingClasses.substring(0, spacingClasses.length - 1);

	            //return <BlockListBlock { ...props } className={ "" + spacingClasses } />;
	            return <BlockListBlock { ...props } className={classnames( props.className, spacingClasses )} />;

			} else {
            	return <BlockListBlock {...props} />
        	}

        } else {
            return <BlockListBlock {...props} />
        }

    };
}, 'withClientIdClassName' );

wp.hooks.addFilter( 'editor.BlockListBlock', 'pwd-blocks/with-client-id-class-name', withSpacingClasses )


///

/**
 * Add custom element class in save element.
 *
 * @param {Object} extraProps     Block element.
 * @param {Object} blockType      Blocks object.
 * @param {Object} attributes     Blocks attributes.
 *
 * @return {Object} extraProps Modified block element.
 */

function applyExtraClass( extraProps, blockType, attributes ) {

	const {
		marginTop,
		marginBottom,
		paddingTop,
		paddingBottom,
	} = attributes;

	/* check if attribute exists for old Gutenberg version compatibility*/
	/* add allowedBlocks restriction */
	if ( typeof marginTop !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, marginTop );
	}

	if ( typeof marginBottom !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, marginBottom );
	}

	if ( typeof paddingTop !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, paddingTop );
	}

	if ( typeof paddingBottom !== 'undefined' && allowedBlocks.includes( blockType.name ) ) {
		extraProps.className = classnames( extraProps.className, paddingBottom );
	}

	return extraProps;
}

//add filters

addFilter(
	'blocks.registerBlockType',
	'pwd-blocks/custom-attributes',
	addAttributes
);

addFilter(
	'editor.BlockEdit',
	'pwd-blocks/custom-advanced-control',
	withAdvancedControls
);

addFilter(
	'blocks.getSaveContent.extraProps',
	'pwd-blocks/applyExtraClass',
	applyExtraClass
);


